/* Hamburgers
@description Tasty CSS-animated hamburgers
@author Jonathan Suh @jonsuh
@site https://jonsuh.com/hamburgers
@link https://github.com/jonsuh/hamburgers */

/* Hamburger */

.hamburger {
  padding: 20px 20px;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  color: var(--light);
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;
}
.hamburger:hover {
  opacity: 0.7;
}
.hamburger.is-active:hover {
  opacity: 0.7;
}
.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::before,
.hamburger.is-active .hamburger-inner::after {
  background-color: #fff;
}

.hamburger-box {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.hamburger-inner {
  display: block;
  top: 50%;
  margin-top: -2px;
}
.hamburger-inner,
.hamburger-inner::before,
.hamburger-inner::after {
  width: 40px;
  height: 4px;
  background-color: var(--light);
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}
.hamburger-inner::before,
.hamburger-inner::after {
  content: '';
  display: block;
}
.hamburger-inner::before {
  top: -10px;
}
.hamburger-inner::after {
  bottom: -10px;
}

.hamburger--spin .hamburger-inner {
  transition-duration: 0.22s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
.hamburger--spin .hamburger-inner::before {
  transition: top 0.1s 0.25s ease-in, opacity 0.1s ease-in;
}
.hamburger--spin .hamburger-inner::after {
  transition: bottom 0.1s 0.25s ease-in,
    transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.hamburger--spin.is-active .hamburger-inner {
  transform: rotate(225deg);
  transition-delay: 0.12s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}
.hamburger--spin.is-active .hamburger-inner::before {
  top: 0;
  opacity: 0;
  transition: top 0.1s ease-out, opacity 0.1s 0.12s ease-out;
}
.hamburger--spin.is-active .hamburger-inner::after {
  bottom: 0;
  transform: rotate(-90deg);
  transition: bottom 0.1s ease-out,
    transform 0.22s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.active {
  display: block;
}

/* Title */

.title {
  margin-top: 100px;
  padding: 50px 50px 100px 50px;
}

.title h1 {
  padding: 30px;
  margin: 1px;
  background-color: var(--light);
  color: var(--primary);
  margin-bottom: 20px;
  font-size: 27px;
  text-align: center;
}

.title p {
  background-color: white;
  margin-bottom: 0;
}

/* Portfolio */

.centered {
  text-align: center;
  color: var(--primary);
  padding: 10px;
}

.glide__arrow {
  color: var(--quaternary);
}

/* Services */

.services {
  text-align: center;
  margin-bottom: 50px;
  padding: 0 50px;
}

.services h2 {
  padding-bottom: 50px;
}

.icons {
  color: var(--primary);
}

.icons h3 {
  font-size: 28px;
  margin-bottom: 0;
}

.icons i {
  color: var(--secondary);
  font-size: 64px;
  padding: 10px;
}

.icons p {
  color: var(--quaternary);
}

/* Lab */

.lab {
  margin-bottom: 20px;
}

.lab-container {
  position: relative;
  display: block;
  margin: 0 auto;
}

.lab-item-1 {
  height: 600px;
}

.lab-item-2 {
  height: 400px;
}

.lab-item-3 {
  height: 200px;
}

.lab img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

/* About */

.about {
  background-color: var(--secondary);
  margin-bottom: 20px;
  padding: 20px 50px 100px 50px;
}

.about h2 {
  background-color: var(--light);
}

.about p {
  background-color: var(--light);
  margin-bottom: 0;
}

/* Footer */

/* Contact */

.footer-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 0;
}

.contact {
  background-color: var(--tertiary);
  padding: 20px 50px 20px 50px;
  margin: 0;
}

.contact h2 {
  background-color: var(--light);
}

.contact-logo {
  width: 200px;
  padding: 20px 0 0 10px;
}

.footer-container p {
  background-color: var(--light);
  margin: 0;
}

.footer-social {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.footer-social a {
  color: var(--primary);
  font-size: 64px;
  padding: 10px;
}

.privacy p {
  text-align: center;
}

.privacy p:hover {
  text-decoration: underline;
}

/* Cookies Notice */

#cookies-eu-banner {
  background-color: var(--tertiary);
  padding: 20px;
  opacity: 0.8;
  color: var(--primary);
}

#cookies-eu-more:hover {
  text-decoration: underline;
}

#cookies-eu-banner button {
  color: white;
  background-color: var(--primary);
  border: var(--primary);
  border-radius: 4px;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  padding: 5px;
  margin-right: 5px;
  margin: 5px;
}

#cookies-eu-banner #cookies-eu-reject {
  background-color: rgb(244, 67, 54);
  border: rgb(244, 67, 54);
}

/* Privacy notice - separate page */

.privacy-header {
  padding: 20px;
}

.privacy-notice h4 {
  color: var(--secondary);
  margin-left: 20px;
}

#cookie-law {
  background-color: red;
  position: fixed;
}

/* Tablet ----------------------------- */

@media (min-width: 569px) {
  /* Header */
  header {
    border-bottom-left-radius: 30%;
  }

  .title h1 {
    font-size: 45px;
  }

  /* Icons */
  .icons {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }

  /* Lab */
  .lab-item-1 {
    width: 1000px;
  }
  .lab-item-3 img {
    border-bottom-right-radius: 40%;
  }
  .about {
    border-bottom-left-radius: 30%;
  }
  .contact {
    border-bottom-right-radius: 30%;
  }
}

/* DESKTOP ----------------------------- */
@media (min-width: 768px) {
  .title h1 {
    font-size: 35px;
  }

  h2 {
    display: flex;
  }

  .portfolio h2 {
    margin-left: 20px;
  }

  .services h2 {
    justify-content: center;
    padding: 50px 0 50px 0;
  }

  .contact h2 {
    margin-left: 30px;
  }

  .navbar {
    display: flex;
    justify-content: space-between;
  }

  .main-nav {
    display: flex;
    margin: 0;
    padding-right: 20px;
    background-color: transparent;
  }

  .mobile-container {
    background-color: var(--secondary);
  }

  .navbar-toggle {
    display: none;
  }

  .nav-links {
    font-size: 14px;
    margin-left: 10px;
    /* opacity: 0.8; */
  }

  a.nav-links {
    position: relative;
  }

  a.nav-links:before {
    content: '';
    position: absolute;
    width: 0;
    height: 5px;
    bottom: -10px;
    left: 0;
    background-color: var(--quaternary);
    visibility: hidden;
    transition: all 0.3s ease-in-out;
    transform: skewY(-5deg);
  }

  a.nav-links:hover:before {
    visibility: visible;
    width: 100%;
  }

  /* Lab */

  .lab {
    display: flex;
  }

  .image-container {
    justify-content: center;
  }

  .footer-container {
    align-items: flex-start;
    /* margin-left: 30px; */
  }
  .footer-social {
    display: inline-block;
  }
  .contact h2 {
    margin-left: 0;
  }
  .privacy p {
    text-align: left;
    padding: 10px;
  }
}

/* Large Screen Desktop */
@media (min-width: 960px) {
  /* Services */

  .icons {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    justify-items: center;
  }
}
